h1.heading {
  font-size: 60px;
  text-align: center;
  font-family: Poppins, sans-serif;
  font-weight: 700;
  margin-bottom: 10px;
}

p.heading.p__color {
  margin-bottom: 0;
  text-align: center;
  line-height: 1.2;
}

.service__box {
  border-radius: 10px;
  width: 100%;
  background-color: #191919;
  padding: 55px;
  transition: 4s ease;
}

.service__box:hover {
  transform: translateY(5px);
  background-image: linear-gradient(90deg, #4B1B86, #ffffff00);
}

.service__box:hover .icon {
  color: #ffc801;
}

.service__box:hover p.service__text.p__color {
  color: #fff !important;
}

.icon {
  font-size: 54px;
  font-weight: 400;
  margin-bottom: 23px;
  display: inline-flex;
  color: #4B1B86;
}

p.service__text.p__color {
  margin-bottom: 0;
}

h1.service__text {
  font-size: 19px;
  font-family: Poppins, sans-serif;
  padding: 10px 0px;
}

/* RESPONSIVE */
@media only screen and (max-width: 1024px) {
  .service__box {
    padding: 32px;
  }
}